import BaseApiService from './api'

export default class WineApiService extends BaseApiService {

    static _instance;
    static instance() {
        if (!this._instance) this._instance = new WineApiService(new SingleLock())
        return this._instance
    }

    constructor(lock) {
        super(); // exception thrown here when not called
        if (lock instanceof SingleLock === false) {
            throw new Error('Sorry but this is singleton')
        }
    }

    getWineDetails(wineObj) {
        return this.execute('get', `/WineDetails?wineId=${wineObj.wineId}&caseSizeId=${wineObj.caseSize}`)
    }

    getFavouriteWines({from, to}) {
        return this.execute('get', '/Favorites')
    }

    setFavouriteWine(wineId) {
        return this.execute('post', `/Favorites?wineId=${wineId}`)
    }

    updateFavouritePosition(wineId, position) {
      return this.execute('put', `/Favorites?wineId=${wineId}&position=${position}`)
    }

    deleteFavouriteWine(wineId) {
      return this.execute('delete', `/Favorites?wineId=${wineId}`)
  }
}

class SingleLock { }
