import BaseApiService from './api'

export default class AuthApiService extends BaseApiService {

    static _instance;
    static instance() {
        if (!this._instance) this._instance = new AuthApiService(new SingleLock())
        return this._instance
    }

    constructor(lock) {
        super(); // exception thrown here when not called
        if (lock instanceof SingleLock === false) {
            throw new Error('Sorry but this is singleton')
        }
    }

    signup(data) {
        return this.execute('post', '/Registration', data, undefined, 'devauth')
    }

    signin(data) {
        return this.execute('post', '/token', data, '/connect', 'devauth', true)
    }

}

class SingleLock { }