import BaseApiService from './api';

export default class MarketApiService extends BaseApiService {
    static _instance;
    static instance() {
        if (!this._instance)
            this._instance = new MarketApiService(new SingleLock());
        return this._instance;
    }

    constructor(lock) {
        super(); // exception thrown here when not called
        if (lock instanceof SingleLock === false) {
            throw new Error('Sorry but this is singleton');
        }
    }

    getProducers(producerName) {
      let params = {
          from: 0,
          to: 10,
          name: producerName
      };

      return this.execute('get', '/Producer', undefined, undefined, undefined, false, params)
    }

    getWines(body) {
        return this.execute('post', '/Market', body);
    }
}

class SingleLock { }