import API from '../services/userApiService';
import { AUser, ANotification } from './actions';
import { MUser } from './mutations';
import { GUser } from './getters';
import axios from "axios";

export default {
  state: {
    addresses: [],
    userData: {},
    commission: {},
    userBalance: 0,
    userHoldings: 0
  },
  getters: {
    [GUser.GET_ADDRESSES](state) {
      return state.addresses || []
    },
    [GUser.GET_USER_DATA](state) {
      return state.userData || {}
    },
    [GUser.GET_USER_MAX_MARKUP_COMMISSION] (state) {
      return state.commission.markupMax ? state.commission.markupMax : 0
    },
    [GUser.GET_USER_MIN_MARKUP_COMMISSION] (state) {
      return state.commission.markupMin ? state.commission.markupMin : 0
    },
    [GUser.GET_USER_MAX_BUY_COMMISSION] (state) {
      return state.commission.buyCommissionMax ? state.commission.buyCommissionMax : 0
    },
    [GUser.GET_USER_MAX_SELL_COMMISSION] (state) {
      return state.commission.sellCommissionMax ? state.commission.sellCommissionMax : 0
    },
    [GUser.GET_USER_MIN_BUY_COMMISSION] (state) {
      return state.commission.buyCommissionMin ? state.commission.buyCommissionMin : 0
    },
    [GUser.GET_USER_MIN_SELL_COMMISSION] (state) {
      return state.commission.sellCommissionMin ? state.commission.sellCommissionMin : 0
    },
    [GUser.GET_USER_SELL_COMMISSION] (state) {
      return state.commission.sellCommission ? state.commission.sellCommission : 0
    },
    [GUser.GET_USER_BUY_COMMISSION] (state) {
      return state.commission.buyCommission ? state.commission.buyCommission : 0
    },
    [GUser.GET_USER_MARKUP_COMMISSION] (state) {
      return state.commission.markup ? state.commission.markup : 0
    },
    [GUser.GET_USER_BALANCE](state) {
      return state.userBalance || 0
    },
    [GUser.GET_USER_HOLDINGS](state) {
      return state.userHoldings
    }
  },
  mutations: {
    [MUser.SET_USER_COMMISSION] (state, commission) {
      state.commission = commission
    },
    [MUser.SET_ADDRESSES](state, addresses) {
      state.addresses = addresses
    },
    [MUser.SET_USER_DATA](state, data) {
      state.userData = data
    },
    [MUser.SET_USER_BALANCE](state, balance) {
      state.userBalance = balance
    },
    [MUser.SET_USER_HOLDINGS](state, holdings) {
      state.userHoldings = holdings
    }
  },
  actions: {
    async [AUser.GET_USER_COMMISSION] ({commit, state}) {
      let commission = await API.instance().getUserCommission();
      commit(MUser.SET_USER_COMMISSION, commission);
    },
    async [AUser.UPDATE_ADDRESS]({ commit, state, rootState }, address) {
      let token = await localStorage.getItem('access_token')
      let headers = {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      }
      let url = `https://devapi.vindome.net/api/Address`

      return new Promise((resolve, reject) => {
        axios.put(url, address, { headers: headers }).then(() => resolve()).catch(() => reject())
      })

    },
    async [AUser.ADD_ADDRESS]({ commit, state, rootState }, address) {
      let token = await localStorage.getItem('access_token')
      let headers = {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      }
      let url = `https://devapi.vindome.net/api/Address`

      return new Promise((resolve, reject) => {
        axios.post(url, address, { headers: headers }).then(() => resolve()).catch(() => reject())
      })

    },
    async [AUser.DELETE_ADDRESS]({ commit, state, rootState }, address) {
      let token = await localStorage.getItem('access_token')
      let headers = {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      }
      let url = `https://devapi.vindome.net/api/Address?addressId=${address.id}`

      return new Promise((resolve, reject) => {
        axios.delete(url, { headers: headers }).then(() => resolve()).catch(() => reject())
      })
    },
    async [AUser.GET_ADDRESSES]({ commit, state, rootState }) {
      let token = await localStorage.getItem('access_token')
      let headers = {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      }
      axios.get(`https://devapi.vindome.net/api/Address`, { headers: headers }).then(r => {
        commit(MUser.SET_ADDRESSES, r.data)
      }).catch(e => {
        commit(MUser.SET_ADDRESSES, [])

      })
    },
    async [AUser.GET_USER_DATA]({ commit, state }) {
      let userData = await API.instance().getUserData();
      userData.phone = ''
      userData.callingCode = ''
      if(userData.phoneNumber) {
        let indx = userData.phoneNumber.indexOf(' ')
        if(indx > -1)
        {
          let arr = userData.phoneNumber.split(' ')
          if(arr.length > 1){
            userData.phone = userData.phoneNumber.substr(indx+1)
            userData.callingCode = arr[0]
          }
        }
      }
      userData.fullName = `${userData.firstName} ${userData.lastName}`
      commit(MUser.SET_USER_DATA, userData)
    },
    [AUser.SAVE_USER_DATA] ({ commit, state, dispatch }) {
      let payload = {
        fullName: state.userData.fullName,
        dateOfBirth: state.userData.dateOfBirth,
        gender: state.userData.gender,
        phoneNumber: state.userData.callingCode + ' ' + state.userData.phone,
        countryId: state.userData.countryId
      }
      return API.instance().saveUserData(payload).then(() => {
        dispatch(ANotification.ADD_NOTIFICATION, {message: 'Data saved!'})
      }).catch(err => {
        console.log(err)
        dispatch(ANotification.ADD_NOTIFICATION, {error: "Data didn't save!"})
      })
    },
    [AUser.GET_USER_BALANCE] ({ commit }) {
      return API.instance().getUserBalance().then(res => { 
        commit(MUser.SET_USER_BALANCE, res[0].amount)
        commit(MUser.SET_USER_HOLDINGS, res[0].holdings)
      })
    },
    [AUser.CHANGE_PASS] ({ commit, dispatch, state }, {oldPass, newPass}) {
      let payload = {
        userName: state.userData.email,
        oldPassword: oldPass,
        newPassword: newPass
      }
      return API.instance().changePass(payload).then(res => {
        dispatch(ANotification.ADD_NOTIFICATION, {message: 'Password has been changed!'})
      }).catch(err => {
        console.log(err.data[0].message)
        dispatch(ANotification.ADD_NOTIFICATION, {error: err.data[0].message})
      })
    },
    [AUser.CONFIRM_EMAIL] ({ state, dispatch }) {
      console.log(state.userData.email)
      return API.instance().confirmEmail({email: state.userData.email}).then(res => {
        console.log(res)
        dispatch(ANotification.ADD_NOTIFICATION, {message: 'Check your email!'})
      })
    }
  }
}