import API from '../services/orderApiService'
import { AOrders } from './actions'
import { MOrders } from './mutations'
import { GOrders } from './getters'

export default {
    state: {
      orders: [],
      activeOrder: {},
      newOrder: {
        pricePerCase: 0,
        isBuy: true,
        barCodes: null,
        uniqueCasesIds: [],
        wineId: 0,
        matchIds: [],
        caseQuantity: 0,
        type: 0
      }
    },
    getters: {
      [GOrders.GET_BIDS_AND_OFFERS] (state) {
        return state.orders
      },
      [GOrders.GET_ACTIVE_ORDER] (state) {
        return state.activeOrder
      },
      [GOrders.NEW_ORDER] (state) {
        return state.newOrder || {}
      }
    },
    mutations: {
      [MOrders.SET_ACTIVE_ORDER] (state, order) {
        state.activeOrder = order
      },
      [MOrders.SET_BIDS_AND_OFFERS](state, orders) {
        state.orders = orders
      },
      [MOrders.SET_CURRENT_ORDER] (state, order) {
        state.newOrder = order
      },
    },
    actions: {
      [AOrders.GET_ORDERS] ({ commit, state }) {
        return API.instance().getOrders().then(res => {
          commit(MOrders.SET_BIDS_AND_OFFERS, res.list.map(order => {
            return {
                key: order.pendingOrderId,
                id: order.pendingOrderId,
                name: order.wineName,
                producer: order.producerName,
                vintage: order.vintage,
                region: order.regionName,
                price: order.pricePerCase + order.markupPerCase,
                units: order.caseQuantity,
                orderType: order.isBuy ? 'BID': 'OFFER',
                wineId: order.wineId,
                size: order.caseSize,
                caseSize: order.caseSizeId,
                createdOn: order.createdOn,
                orderStatus: order.status
            }
          }))
        })
      },
      [AOrders.SAVE_ORDER] ({ commit, state }, {price, units}) {
        console.log(price, units)
        return API.instance().saveOrder({
          pricePerCase: price,
          quantity: units,
          pedningOrderId: state.activeOrder.id
        })
      },
      [AOrders.CANCEL_ORDER] ({ commit, state }, id) {
        return API.instance().cancelOrder(id)
      },
      [AOrders.SET_ACTIVE_ORDER] ({commit, state}, order) {
        commit(MOrders.SET_ACTIVE_ORDER, order)
      },
      [AOrders.CREATE_ORDER] ({ commit, state }, order) {
        return API.instance().createOrder(order)
      },
      [AOrders.SET_CURRENT_ORDER] ({commit, state}, order) {
        commit(MOrders.SET_CURRENT_ORDER, order);
      }
    }
  }