import BaseApiService from './api'

export default class OrderApiService extends BaseApiService {

    static _instance;
    static instance() {
        if (!this._instance) this._instance = new OrderApiService(new SingleLock())
        return this._instance
    }

    constructor(lock) {
        super(); // exception thrown here when not called
        if (lock instanceof SingleLock === false) {
            throw new Error('Sorry but this is singleton')
        }
    }

    getOrders(wineId = undefined) {
        let params = {
            from: 0,
            to: 10,
            wineId
        }
        return this.execute('get', '/PendingOrders', undefined, undefined, undefined, false, params)
    }

    saveOrder(data) {
        return this.execute('put', '/PendingOrders', data)
    }

    cancelOrder(id) {
        return this.execute('delete', '/PendingOrders?orderId=' + id)
    }

    createOrder(data) {
        return this.execute('post', '/PendingOrders', data)
    }
}

class SingleLock { }