import {ANotification} from './actions'
import {MNotification} from './mutations'
import {GNotification} from './getters'

export default {
    state: {
      modalVisible: false,
      message: '',
      error: ''
    },
    getters: {
        [GNotification.GET_VISIBLE] (state) {
            return state.modalVisible
        },
        [GNotification.GET_MESSAGE] (state) {
            return state.message
        },
        [GNotification.GET_ERROR] (state) {
            return state.error
        },
    },
    mutations: {
      [MNotification.SET_VISIBLE] (state, visible) {
        state.modalVisible = visible
      },
      [MNotification.SET_MESSAGE] (state, message) {
        state.message = message
      },
      [MNotification.SET_ERROR] (state, error) {
        state.error = error
      },
    },
    actions: {
      [ANotification.ADD_NOTIFICATION] ({commit}, {message, error}) {
        commit(MNotification.SET_VISIBLE, true)
        commit(MNotification.SET_MESSAGE, message)
        commit(MNotification.SET_ERROR, error)
      },
      [ANotification.REMOVE_NOTIFICATION] ({commit}) {
        commit(MNotification.SET_VISIBLE, false)
        commit(MNotification.SET_MESSAGE, '')
        commit(MNotification.SET_ERROR, '')
      }
    }
}