import BaseApiService from './api'

export default class UserApiService extends BaseApiService {

    static _instance;
    static instance() {
        if (!this._instance) this._instance = new UserApiService(new SingleLock())
        return this._instance
    }

    constructor(lock) {
        super(); // exception thrown here when not called
        if (lock instanceof SingleLock === false) {
            throw new Error('Sorry but this is singleton')
        }
    }

    getUserCommission() {
      return this.execute('get', '/tradingSettings')
    }

    getUserData() {
        return this.execute('get', '/users')
    }

    saveUserData(data) {
        return this.execute('put', '/users', data)
    }

    getUserBalance () {
        return this.execute('get', '/Balance')
    }

    changePass (data) {
        return this.execute('post', '/Password', data)
    }

    confirmEmail(data) {
        return this.execute('post', '/EmailConfirmation', data, undefined, 'devauth')
    }

}

class SingleLock { }