
export default class AuthDataLayer {
    static _instance;
    static instance() {
        if (!this._instance) this._instance = new AuthDataLayer(new SingleLock())
        return this._instance
    }
    authServiceUrl = 'https://devauth.vindome.net' // process.env.serviceUrl
    connection = null

    constructor(lock) {
        if (lock instanceof SingleLock === false) {
            throw new Error('Sorry but this is singleton')
        }
    }

    getToken(o) {
        let u = `${this.authServiceUrl}/connect/token`
        let data = Object.keys(o).map(key => key + '=' + o[key]).join('&');
        console.log('calling ' + u)
        console.log('data ' + data)
        return fetch(u, {
            method: 'POST',
            headers:{'content-type': 'application/x-www-form-urlencoded'},
            body: data
        })
    }
}

class SingleLock { }
