import moment from 'moment'
import API from '../services/portfolioApiService'
import { APortfolio, AUser } from './actions'
import { MPortfolio } from './mutations'
import { GPortfolio, GUser } from './getters'

export default {
    state: {
        wineList: []
    },
    mutations: {
        [MPortfolio.SET_PORTFOLIO_WINE_LIST] (state, list) {
            state.wineList = list
        },
    },
    getters: {
        [GPortfolio.GET_PORTFOLIO_WINE_LIST] (state) {
            return state.wineList
        },
        [GPortfolio.GET_PORTFOLIO_TOTAL_PROFIT] (state) {
            let profit = 0
            state.wineList.forEach(item => {
                let marketValue = item.marketValue
                let price = item.price
                profit += marketValue !== 0 ? (marketValue - price) : 0
            })
            return profit
        },
        [GPortfolio.GET_PORTFOLIO_VALUE] (state, getters) {
            return getters[GPortfolio.GET_PORTFOLIO_HOLDINGS] + getters[GUser.GET_USER_BALANCE]
        },
        [GPortfolio.GET_PORTFOLIO_HOLDINGS] (state, getters) {
            // let value = 0
            // if (state.wineList.length) value = state.wineList.map(item => {
            //     return item.price
            // }).reduce((acc, a) => {
            //     return acc + a
            // })
            // return value
            return getters[GUser.GET_USER_HOLDINGS]
        },
    },
    actions: {
        [APortfolio.GET_PORTFOLIO] ({ commit, dispatch }) {
            dispatch(APortfolio.LOAD_WINES)
            dispatch(AUser.GET_USER_BALANCE)
        },
        [APortfolio.REMOVE_WINES] ({ commit, dispatch }) {
            return commit(MPortfolio.SET_PORTFOLIO_WINE_LIST, [])
        },
        [APortfolio.LOAD_WINES] ({ commit, state }) {
            //let wineListLength = state.wineList.length
            API.instance().getPortfolio({
                from: 0 ,//wineListLength !== 0 ? wineListLength + 1 : 0,
                to: 100 //wineListLength + 100
            }).then(res => {
                let newWineList = res.list.map(item => {
                    item.units = item.units;
                    item.percentageDiffernece = (Math.random() * (100 - -100) + -100).toFixed(1);
                    item.wine.size = `${item.bottlesInside}x${item.bottleSize}`;
                    item.wine.date = moment(item.dateOn).format('DD.MM.YYYY H:mm');
                    return item;
                })
                //commit(MPortfolio.SET_PORTFOLIO_WINE_LIST, state.wineList.concat(newWineList))
                commit(MPortfolio.SET_PORTFOLIO_WINE_LIST, newWineList);
            })
        }
    }
}