<template>
    <span class="header-container">
        <v-navigation-drawer app v-model="drawer" class="header-toolbar" disable-resize-watcher>
            <v-list>
                <template v-for="(item, index) in items">
                    <v-list-item :key="index">
                        <v-list-item-content>
                            {{item.title}}
                        </v-list-item-content>
                    </v-list-item>
                    <v-divider :key="`divider-${index}`"></v-divider>
                </template>
            </v-list>
        </v-navigation-drawer>
        <v-app-bar app dark class="header-toolbar">
            <v-app-bar-nav-icon class="hidden-md-and-up" @click="drawer = !drawer"></v-app-bar-nav-icon>
            <v-spacer class="hidden-md-and-up"></v-spacer>
            <div class="logo">
                <a href="/">
                    <img class="header-logo" src="../assets/logo.png" alt="Vindome logo">
                </a>
            </div>
            <ul class="navs-list site-navigation">
                <li><router-link to="/howitworks">How It Works</router-link></li>
                <li><router-link to="/tradingdesk">Trading Desk</router-link></li>
                <li><router-link to="/investmenttips">Investment Tips</router-link></li>
                <li><router-link to="/wineguide">Wine Guide</router-link></li>
            </ul>
            <v-spacer class="hidden-sm-and-down"></v-spacer>
            <router-link v-if="userData.email == null" to="/login" class="header-login-btn">Sign In</router-link>
            <v-btn v-if="userData.email == null" class="hidden-sm-and-down header-register-btn"><router-link to="/register">Get Started</router-link></v-btn>
            <v-btn v-if="userData.email != null" class="hidden-sm-and-down header-register-btn"><router-link to="/myaccount">My Account</router-link></v-btn>
        </v-app-bar>
    </span>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex';
    import { AUser } from '../store/actions';
    import { GUser } from '../store/getters';

    export default {
        name: 'AppNavigation',
        data() {
            return {
                appTitle: 'Vindome',
                drawer: false,
                isLogged: {},
                items: [
                    { title: 'How It Works' },
                    { title: 'Trading Desk' },
                    { title: 'Investment Tips' },
                    { title: 'Wine Guide' },
                    { title: 'Sign In' },
                    { title: 'Get Started' }
                ]
            };
        },
        computed: {
            ...mapGetters({
                userData : GUser.GET_USER_DATA
            })
        },
        methods: {
            ...mapActions({
                getData: AUser.GET_USER_DATA
            }),
        },
        created () {
            this.getData();
        }
    };
</script>

<style scoped>
    .header-toolbar {
        background-color: #023E61 !important;
        border-color: #023E61 !important;
        min-height: 61px;
        font-family: 'Montserrat', Arial, Helvetica, sans-serif;
        font-size: 14px;
    }

    .header-login-btn {
        border-bottom: 1px solid transparent;
        text-transform: uppercase;
        text-decoration: none;
        font-weight: 600;
        color: #fff;
        margin-right: 15px;
        -webkit-transition: border .3s ease-out;
        -o-transition: border .3s ease-out;
        transition: border .3s ease-out;
    }
    .header-login-btn:hover {
        border-color: #ffffff;
    }

    .header-register-btn {
        background-color: #DEBA65!important;
        border-color: #DEBA65!important;
    }

    .logo {
        position: relative;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
        -ms-flex-direction: row;
        flex-direction: row;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        width: 300px;
        min-height: 64px;
        text-align: center;
        background-color: #024E7B;
        z-index: 1;
        margin-right: 60px;
    }

    .logo::after {
        content: '';
        display: block;
        height: 100%;
        position: absolute;
        right: -20px;
        bottom: 0;
        left: -25px;
        background: inherit;
        -webkit-transform: skewX(20deg);
        -ms-transform: skewX(20deg);
        transform: skewX(20deg);
        -webkit-transform-origin: 100%;
        -ms-transform-origin: 100%;
        transform-origin: 100%;
        z-index: -1;
    }

    .logo img {
        width: 156px;
    }

    .main-navigation {
        position: relative;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
        -ms-flex-direction: row;
        flex-direction: row;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
        justify-content: space-between;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        min-height: 66px;
    }

    .navs-list {
        position: relative;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
        -ms-flex-direction: row;
        flex-direction: row;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
        justify-content: space-between;
        padding: 0;
        list-style: none;
    }

    .navs-list > li {
        position: relative;
    }

    .navs-list > li .dropdown-toggle {
        padding-right: 20px;
    }

    .navs-list > li .dropdown-toggle::after {
        content: '';
        position: absolute;
        top: -2px;
        right: 2px;
        display: inline-block;
        width: 8px;
        height: 8px;
        border-top: 1px solid #fff;
        border-right: 1px solid #fff;
        -webkit-transform: rotate(135deg);
        -ms-transform: rotate(135deg);
        transform: rotate(135deg);
    }

    .navs-list > li.open .dropdown-toggle::after {
        top: 2px;
        -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
        transform: rotate(-45deg);
    }

    .site-navigation {
        width: 510px;
    }

    .navs-list > li a {
        border-bottom: 1px solid transparent;
        text-transform: uppercase;
        text-decoration: none;
        font-size: 12px;
        font-weight: 600;
        color: #fff;
        -webkit-transition: border .3s ease-out;
        -o-transition: border .3s ease-out;
        transition: border .3s ease-out;
    }

    .navs-list > li a:hover {
        border-color: #fff;
    }

    .nav-second-level {
        min-width: 150px;
        padding: 8px 0;
        border-radius: 10px;
        background-color: #013E61;
    }

    .nav-second-level > li a {
        display: block;
        padding: 12px 20px;
        word-wrap: normal;
        color: #598198;
    }

    .nav-second-level > li a:hover {
        color: #fff;
    }

    .dropdown-menu {
        position: absolute;
        top: 25px;
        /* top: 100%; */
        left: -20px;
        right: auto;
        display: none;
        z-index: 1000;
    }

    .dropdown-menu > li > a {
        text-transform: uppercase;
    }

    .open > .dropdown-menu {
        display: block;
    }

    .site-navigation {
        font-size: 12px;
    }

    .settings-navigation {
        width: 124px;
        font-size: 10px;
    }

    .user-navigation {
        width: 209px;
        font-size: 14px;
    }

    .header-register-btn a {
        color: #FFFFFF;
        text-decoration: none;
    }
</style>