import {ACommon} from './actions'
import {GCommon} from './getters'
import {MCommon} from './mutations'

import axios from "axios";

export default {
  state: {
    countriesList: [],
    currencySign: '',
    shouldMarketUpdate: true
  },
  mutations: {
    [MCommon.SET_SHOULD_MARKET_UPDATES] (state, variant) {
      state.shouldMarketUpdate = variant
    },
    [MCommon.SET_ALL_COUNTRIES] (state, list) {
      state.countriesList = list
    }
  }, 
  actions: {
    async [ACommon.GET_ALL_COUNTRIES] ( {commit} ) {
      let token = await localStorage.getItem('access_token'),
          headers = {
              'Content-Type': 'application/json',
              'Authorization': `Bearer ${token}`
          },
          url = `https://devapi.vindome.net/api/Filters/getAllCountries`;

      axios.get(url, { headers: headers })
        .then(response => commit(MCommon.SET_ALL_COUNTRIES, response.data))
        .catch(err => {
          console.log(err);
          commit(MCommon.SET_ALL_COUNTRIES, [])
        })
    }
  },
  getters: {
    [GCommon.GET_SHOULD_MARKET_UPDATES] (state) {
      return state.shouldMarketUpdate
    },
    [GCommon.GET_COUNTRIES_LIST] (state) {
      return state.countriesList
    },
    [GCommon.GET_CURRENCY_SIGN] (state) {
      return state.currencySign
    }
  }
}